#search-form {
  width: 100%;
  height: 100%;
  background: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

#search-form:hover > .search-input {
  scale: 1;
}

#search-form:hover > .query-btn {
  transform: translateX(-60px);
}

@media screen and (min-width: 768px) {
  #search-form:hover > .query-btn {
    transform: translateX(-80px);
  }
}

@media screen and (min-width: 1280px) {
  #search-form:hover > .query-btn {
    transform: translateX(-110px);
  }
}

.query-btn {
  float: left;
  width: 20px;
  height: 20px;
  background: none;
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  transform: translateX(350%);
}

@media screen and (min-width: 768px) {
  .query-btn {
    transform: translateX(-80px);
  }
}

@media screen and (min-width: 1280px) {
  .query-btn {
    transform: translateX(-110px);
  }
}

.search-input {
  float: left;
  height: 100%;
  width: 100%;
  letter-spacing: .02em;
  color: #111321;
  opacity: .4;
  background: none;
  border: 1px solid #111321;
  border-radius: 20px;
  outline: none;
  padding: 5px 5px 5px 48px;
  font-family: Poppins, sans-serif;
  font-size: 12px;
  line-height: 1.75;
  transition: scale .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
  scale: 0;
}

@media screen and (min-width: 768px) {
  .search-input {
    font-size: 14px;
    line-height: 1.71;
    scale: 1;
  }
}

.header-list__item {
  position: relative;
}

.header-list__link--current {
