}

.header-list__link--current:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #4440f7;
  border: 1px solid #4440f7;
  border-radius: 2px;
  position: absolute;
  bottom: -5px;
  left: 0;
}

.menu-toggle {
  min-height: 24px;
  min-width: 24px;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

@media screen and (min-width: 768px) {
