.menu-container {
  width: 100vw;
  height: 100%;
  z-index: 999;
  background-color: #f4f4f4;
  flex-direction: column;
  justify-content: space-between;
  padding: 24px 16px;
  transition: scale .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  scale: 0;
}

.menu-container.is-open {
