}

.menu-bar__link {
  color: #111321;
  margin-left: 14px;
  margin-right: auto;
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.38;
}

.menu-bar__icons {
