.vanilla-calendar {
  width: 250px;
  height: 250px;
  min-width: min-content;
  background-color: #fff;
  border-radius: 20px;
  grid-template-columns: 1fr;
  gap: 12px;
  padding: 12px;
  font-family: Manrope, sans-serif;
  display: grid;
  position: relative;
}

.vanilla-calendar_multiple .vanilla-calendar-month {
  color: #000;
  cursor: default;
}

.vanilla-calendar_multiple .vanilla-calendar-month:hover {
  color: #000;
}

.vanilla-calendar_multiple .vanilla-calendar-month_disabled {
  color: #000;
  pointer-events: auto;
}

.vanilla-calendar_multiple .vanilla-calendar-year {
  color: #000;
