.vanilla-calendar-wrapper {
  height: 200px;
  gap: 2px;
  display: flex;
}

.vanilla-calendar-content {
  flex-direction: column;
  flex-grow: 1;
  display: flex;
}

.vanilla-calendar-arrow {
  pointer-events: auto;
  width: 18px;
  height: 18px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  border-radius: 4px;
  padding: 2px;
  font-size: 0;
  display: block;
  position: relative;
  right: 2px;
}

.vanilla-calendar-arrow:before {
  content: "";
  width: 10px;
  height: 1px;
  background-color: #4440f6;
  position: absolute;
  top: 5px;
  left: 5px;
  transform: rotateZ(45deg);
}

.vanilla-calendar-arrow:after {
  content: "";
  width: 10px;
  height: 1px;
  background-color: #4440f6;
  position: absolute;
  bottom: 5px;
  left: 5px;
  transform: rotateZ(-45deg);
}

.vanilla-calendar-arrow_prev {
  left: 4px;
  right: 0;
  transform: rotateZ(180deg);
}

.vanilla-calendar-months {
  background-color: #fff;
  border-radius: 4px;
  flex-grow: 1;
  grid-template-columns: repeat(4, 1fr);
  align-items: center;
  gap: 12px 6px;
