.header-logo {
    font-family: 'Manrope', sans-serif;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.38;
    color: $primary-text-color;
    @include tablet {
        font-size: 32px;
    }
    @include desktop {
        font-size: 42px;
        line-height: 1.36;
    }

}

.nav {
    display: flex;
    padding-top: 24px;
    padding-bottom: 24px;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);


    @include tablet {
        justify-content: space-between;
    }
}

.header-list {
    display: none;

    @include tablet {
        display: flex;
        gap: 44px;
    }

    @include desktop {
        gap: 69px;
    }
}

.header-list__link {
    font-family: 'Manrope', sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.38;
    color: $primary-text-color;

    @include desktop {
        font-size: 18px;
        line-height: 1.39;

    }
}

.change-theme {
    display: none;

    @include tablet {
        display: block;
    }
}

.search-box {
    position: relative;
    width: 173px;
    height: 31px;

    @include tablet {
        width: 213px;
    }

    @include desktop {
        width: 288px;
    }
} 
#search-form {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    background: none;
    &:hover > .search-input {
        // transform:  translateX(0%);
        scale: 1;

    }
    &:hover > .query-btn {
        transform:  translateX(-60px);
        // left: 20px;
        @include tablet {
            transform:  translateX(-80px);
        }
        @include desktop {
            transform:  translateX(-110px);
        }
    }
}

.query-btn {
    padding: 0;
    float: left;
    width: 20px;
    height: 20px;
    background: none;
    border: none;
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.4s;
    transform: translateX(350%);
    transition: transform $duration $cubic;

    @include tablet {
        transform:  translateX(-80px);
    }

    @include desktop {
        transform:  translateX(-110px);
    }
}

.search-input {
    position: absolute;
    top: 0;
    left: 0;
    background: none;
    outline:none;
    float: left;
    height: 100%;
    border: 1px $primary-border-color solid;
    border-radius: 20px;
    font-size: 16px;
    transition: 0.4s;
    width: 100%;
    padding: 5px 5px 5px 48px;
    scale: 0;
    transition: scale $duration $cubic;

    font-family: 'Poppins', sans-serif;
    font-size: 12px;
    line-height: 1.75;
    letter-spacing: 0.02em;
    color: $primary-text-color;
    opacity: 0.4;

    @include tablet {
        scale: 1;
        font-size: 14px;
        line-height: 1.71;
    }
}

.header-list__item {
    position: relative;
}

.header-list__link--current {
    color: currentColor;

    &::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -5px;
    width: 100%;
    height: 1px;
    border: 1px solid $secondary-text-color;
    border-radius: 2px;

    background-color: $secondary-text-color;
    }
}